import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhQuery, VhQuerySales, VhAlgorithm, VhAuth } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { SalesComponent } from 'src/app/sales/components/bill-detail/sales/sales.component';
import { WarrantyComponent } from 'src/app/sales/components/bill-detail/warranty/warranty.component';
import { CustomerReturnComponent } from 'src/app/sales/components/bill-detail/customer-return/customer-return.component';
import { OpenningStockComponent } from 'src/app/sales/components/bill-detail/openning-stock/openning-stock.component';
import { DeliveryGoodsComponent } from 'src/app/sales/components/bill-detail/delivery-goods/delivery-goods.component';
import { RecieveGoodsComponent } from 'src/app/sales/components/bill-detail/recieve-goods/recieve-goods.component';
import { PurchaseComponent } from 'src/app/sales/components/bill-detail/purchase/purchase.component';
import { ReturnSupplierComponent } from 'src/app/sales/components/bill-detail/return-supplier/return-supplier.component';
import { ExchangePaymentCardComponent } from 'src/app/sales/components/bill-detail/exchange-payment-card/exchange-payment-card.component';
import { ExchangeVoucherComponent } from 'src/app/sales/components/bill-detail/exchange-voucher/exchange-voucher.component';
import { DestroyProductComponent } from 'src/app/sales/components/bill-detail/destroy-product/destroy-product.component';

@Component({
  selector: 'app-goods-control',
  templateUrl: './goods-control.component.html',
  styleUrls: ['./goods-control.component.scss']
})
export class GoodsControlComponent implements OnInit {

  public date: Date[] = [new Date(), new Date()];
  public searchValue: string = ''
  public invoiceShow: any[] = []
  private invoiceSegment: any[] = []
  public invoices: any[];
  listFilter: Array<any> = [{ value: 1, name: "Latest" }, { value: 2, name: "Oldest" }, { value: 3, name: "Low to High value" }, { value: 4, name: "High to Low value" }]
  tableHeight: string;
  status: any;
  totalValInvoice: number = 0;
  listTabs = ['Sold', 'Return supplier', 'Output goods for warranty', 'Delivery goods to the branch', 'Input', 'Customer returned', 'Receive goods from the branch', 'Đổi voucher lấy hàng hóa', 'Đổi thẻ trả trước lấy hàng hóa']
  dataRestore: any = null;
  constructor(
    private vhQuery: VhQuery,
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    public vhAuth: VhAuth,
    private cdRef: ChangeDetectorRef,
    private nzModal: NzModalService,
    private datePipe: DatePipe,
    private fncService: FunctionService
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
    this.dataRestore = this.router.getCurrentNavigation().extras.state;
  }

  ngOnInit() {
    if (this.dataRestore) {
      this.searchValue = this.dataRestore.searchValue
      this.filterType = this.dataRestore.filterType
      this.selectedIndex = this.dataRestore.selectedIndex
      this.date = this.dataRestore.date
      this.invoices = this.dataRestore.invoices
      this.segmentChanged(this.selectedIndex)
    } else {
      this.filterType = 1;
    }

  }
  
  /**
   * get các hóa đơn liên quan xuất-nhập hàng
   */
  getInvoices() {
    this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => {
      this.vhQuerySales.getBills_byFields( { date: { $gte: new Date(this.date[0].setHours(0, 0, 0)), $lte: new Date(this.date[1].setHours(23, 59, 59, 59)) }, bill_type: { $in: [1, 10, 15, 30, 2, 3, 31,41,51] }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id } }, {})
        .then((invoice: any[]) => {
          this.invoices = invoice.map(item => {
            if (item.verify_status_bill) item['control_info'] = this.renderControl(item)
            return {
              ...item,
              partner_name: item.id_customer ? this.renderPartner(item.id_customer) : null,
              supplier_name: item.id_supplier ? this.renderSupplier(item.id_supplier) : null,
              payment_name: (item.payment_type && item.id_wallet) ? this.renderPayment(item.payment_type, item.id_wallet) : null,
              name_employee: item.id_employee ? (this.vhAuth.getlocalEmployee(item.id_employee).name) : null,
              branch_name: (item.id_branch && (item.id_branch_send || item.id_branch_receive)) ? this.renderBranch(item) : null,
              total: item.total ? item.total * (1 + item.tax / 100) : 0
            }
          })
          this.segmentChanged(this.selectedIndex);
          this.status = "Not found"
          this.vhComponent.hideLoading(0);
          

        }, err => {
          this.vhComponent.hideLoading(0);
        })
    })
  }

  /**
   * trả về tên ncc của bill
   * @param id_partner 
   * @returns string
   * @example let supplier_name = this.renderSupplier('DNIKWUJHNBDUYWGU') 
   */
  renderSupplier(id_partner: string) {
    let supplier: string = this.vhQuerySales.getlocalSupplier(id_partner).name

    return supplier ? supplier : this.lang.translate("Not found")
  }
  /**
   * trả về tên kh của bill
   * @param id_partner 
   * @returns string
   * @example let customer_name = this.renderPartner('DNIKWUJHNBDUYWGU') 
   */
  renderPartner(id_partner: string) {
    let customer: string = this.lang.translate(this.vhQuerySales.getlocalCustomer(id_partner)?.name)

    return customer ? customer : this.lang.translate("Not found")
  }
  /**
   * xử lý chi nhánh cho bill chuyển/nhận hàng
   */
  renderBranch(bill) {
    if (bill.bill_type == 30) return this.vhQuerySales.getlocalBranch(bill.id_branch).name + " " + this.lang.translate('To branch').toLowerCase() + " " + this.vhQuerySales.getlocalBranch(bill.id_branch_receive).name
    else if (bill.bill_type == 31) return this.vhQuerySales.getlocalBranch(bill.id_branch_send).name + " " + this.lang.translate('To branch').toLowerCase() + " " + this.vhQuerySales.getlocalBranch(bill.id_branch).name;
  }

  /**
   * trả về thông tin đối soát
   * @example let checked = this.renderControl(bill_selected)
   */
  renderControl(bill) {
    return (bill['id_verifier_bill'] && bill['verify_date_bill'] )? `${this.vhAuth.getlocalEmployee(bill['id_verifier_bill'])['name']} ${this.lang.translate('has checked on')} ${this.datePipe.transform(bill['verify_date_bill'], 'dd/MM/yyyy')}` :''
  }
  public filterType: 1 | 2 | 3 | 4 = 1;
   

  sortFilter(filterType: 1 | 2 | 3 | 4) {
    switch (filterType) {
      case 1: this.invoiceSegment.sort((prev, next) => Date.parse(next.date) - Date.parse(prev.date));  break;
      case 2: this.invoiceSegment.sort((prev, next) => Date.parse(prev.date) - Date.parse(next.date));   break;
      case 3: this.invoiceSegment.sort((prev, next) => (prev.total * (1 + prev.tax / 100)) - (next.total * (1 + next.tax / 100)));  break;
      case 4: this.invoiceSegment.sort((prev, next) => (next.total * (1 + next.tax / 100)) - (prev.total * (1 + prev.tax / 100)));  break;
    }

    this.searchBill(this.searchValue)
  }
  /**
   * lọc bill theo ['bill_code', 'partner_name', 'payment_name', 'status', 'name_employee']
   * @example this.searchBill('')
   */
  searchBill(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.invoiceShow = this.vhAlgorithm.searchList(val, this.invoiceSegment, ['bill_code', 'partner_name', 'payment_name', 'name_employee', 'status'])
    } else this.invoiceShow = [...this.invoiceSegment]

  }


  ngAfterViewChecked() {
    if (document.querySelector("#goods-control-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".ant-tabs-nav") && document.querySelector(".invoice-header")) {
      this.tableHeight = document.querySelector("#goods-control-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".invoice-header").clientHeight - document.querySelector(".ant-tabs-nav").clientHeight - 60 + "px";
      if (document.querySelector(".ant-table-pagination")) {
        this.tableHeight = document.querySelector("#goods-control-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
          - document.querySelector(".invoice-header").clientHeight - document.querySelector(".ant-tabs-nav").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 60 + "px";
      }
    }

    this.cdRef.detectChanges();
  }
  /**
   * trả về tên phương thức thanh toán của đơn
   * @example let payment_name= this.renderPayment(1)
   */
  renderPayment(payment_type: 1 | 2 | 3, id_wallet: string = null) {
    switch (payment_type) {
      case 1: return this.lang.translate('Cash')
      case 2: return this.lang.translate('Debit')
      case 3: return this.vhQuerySales.getlocalWallet(id_wallet).name
    }
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/stock"])
  }
  selectedIndex: number = 0;
  /**
   * bắt sk thay đổi tab xem loại hóa đơn
   */
  segmentChanged(value) {
    if (this.invoices) {
      switch (value) {
        case 0: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 1); break;
        case 1: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 10); break;
        case 2: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 15); break;
        case 3: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 30); break;
        case 4: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 2); break;
        case 5: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 3); break;
        case 6: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 31); break;
        case 7: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 41); break;
        case 8: this.invoiceSegment = this.invoices.filter(item => item.bill_type == 51); break;
      }
      this.sortFilter(this.filterType);
    }
  }

  /**
   * bắt sk thay đổi thời gian xem báo cáo
   * @param event 
   * @example this.changeTime(['2023-06-27T01:21:41.397Z','2023-06-27T01:21:41.397Z'])
   */
  changeTime(event) {
    if (new Date(event[1].setHours(0,0,0,0)).getTime() - new Date(event[0].setHours(0,0,0,0)).getTime() <=  this.fncService.checkDateReport() * 86000000) {
      //this.getTransferList();
    } else {
      this.vhComponent.alertMessageDesktop("error", this.lang.translate("The time limit to view is") + " " + this.fncService.checkDateReport() + " " +  this.lang.translate("days"))
      this.date = [new Date(event[0]), new Date(event[0].getTime() + this.fncService.checkDateReport() * 86000000)]
    }
  }

  /**
   * Đối soát bill
   */
  checkedBill(checked, bill) {
    this.nzModal.confirm({
      nzTitle: `<b>${this.lang.translate("Confirm")}?</b>`,
      nzContent: `<i>${checked ? this.lang.translate("Do you want to check this invoice") + "?" : this.lang.translate("Do you want to cancel this invoice check") + "?"}</i>`,
      nzOnOk: () => {
        this.updateBill(bill,checked)
      },
      nzOnCancel: () => {
        bill.verify_status_bill = !checked
      }
    });
  }
  /**cập nhật đối soát */
  updateBill(bill,checked) {
    let value: any = {
      verify_status_bill: checked
    }
    if (checked) {
      value['id_verifier_bill'] = this.vhAuth.getUser()._id
      value['verify_date_bill'] = new Date().toISOString()
    }
    this.vhQuerySales.updateBill(bill._id, value).then(() => {
      if (checked)
        bill['control_info'] = this.renderControl(value)
    })
  }
  /**Vào chi tiết của mỗi hóa đơn */
  gotoDetail(item) {
    
    
    switch (item.bill_type) {
      case 1: {
        return this.vhComponent.showModal(SalesComponent, { id_bill: item._id }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 2: {
        return this.vhComponent.showModal(PurchaseComponent, { id_bill: item._id }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 3: {
        return this.vhComponent.showModal(CustomerReturnComponent, { id_bill: item._id }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 9: {
        return this.vhComponent.showModal(OpenningStockComponent, { id_bill: item._id }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 10: {
        return this.vhComponent.showModal(ReturnSupplierComponent, { id_bill: item._id }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 15: {
        return this.vhComponent.showModal(WarrantyComponent, { id_bill: item._id }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 30: {
        return this.vhComponent.showModal(DeliveryGoodsComponent, { id_bill: item._id }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 31: {
        return this.vhComponent.showModal(RecieveGoodsComponent, { id_bill: item._id }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 41: {
        return this.vhComponent.showModal(ExchangeVoucherComponent, { id_bill: item._id }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 51: {
        return this.vhComponent.showModal(ExchangePaymentCardComponent, { id_bill: item._id }, false, false, 'bill-detail')
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
      }
      case 38: {
        if (!item._id) {
          this.vhComponent.alertMessageDesktop("warning", this.lang.translate("Xuất hủy hàng hóa từ cân bằng kho"))
          break;
        }
        else {
          return this.vhComponent.showModal(DestroyProductComponent, { id_bill: item._id }, false, false, 'bill-detail')
            .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
        }
      }
      case 39: {
        this.vhComponent.alertMessageDesktop("warning", this.lang.translate("Nhập bù hàng hóa từ cân bằng kho"))
        break;
      }
    }
  }


  /** hàm sort cho các cột */
  dateCol = false;
  bill_codeCol = false;
  payment_nameCol = false;
  name_employeeCol = false;
  totalCol = false;
  customer_supplier_branchCol = false;
  /** Hàm thực hiện sắp xếp theo collect
   *
   * @param colName       // tên cột muôn sắp xếp
   */
  sortTable(colName, selectedIndex?){
    switch (colName) {
      case 'date':
        if(this.dateCol){
          this.invoiceShow = this.vhAlgorithm.sortDatebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortDatebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'bill_code':
        if(this.bill_codeCol){
          this.invoiceShow = this.vhAlgorithm.sortStringbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortStringbyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'payment_name':
        if(this.payment_nameCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'name_employee':
        if(this.name_employeeCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'total':
        if(this.totalCol){
          this.invoiceShow = this.vhAlgorithm.sortNumberbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortNumberbyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'different':
        if(this.customer_supplier_branchCol){
          if([0,2,5].includes(selectedIndex)) this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], 'partner_name');
          if([1,4].includes(selectedIndex)) this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], 'supplier_name');
          if([3,6].includes(selectedIndex)) this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], 'branch_name');
        }else{
          if([0,2,5].includes(selectedIndex)) this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], 'partner_name');
          if([1,4].includes(selectedIndex)) this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], 'supplier_name');
          if([3,6].includes(selectedIndex)) this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], 'branch_name');

        }
        break;
    }
  }
}
